.product {
    padding-top: 24px;
    padding-bottom: 21px;

    @media (min-width: 1152px) {
        padding-top: 160px;
        padding-bottom: 100px;
        background-image: url('../img/product-bg.png');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: right top;
        margin-top: -141px;
    }
}

.product__breadcrumbs-list {
    margin-bottom: 21px; 

    @media (min-width: 992px) {
        margin-bottom: 27px; 
    }
}

.product__item {
    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    @media (min-width: 1152px) {
        justify-content: flex-start;
    }
}

.product__title {
    @media (min-width: 768px) {
        width: 100%;
    }
}

.product__info {
    @media (min-width: 768px) {
        width: 250px;
        order: 0;
    }

    @media (min-width: 992px) {
        width: 315px;
       
    }
}

.product__slider {
    @media (min-width: 768px) {
        order: 1;
    }

    @media (min-width: 768px) {

    }
}

.product__wrapper {
    padding: 0 16px;

    @media (min-width: 1152px) {
        padding: 0 27px;
    }
}

.product__slider {
    margin-bottom: 49px;

    @media (min-width: 1152px) {
        align-self: start;
    }
}

.product__slide {
    display: flex;
    justify-content: center;
    align-items: center;
}

.product__text {
    margin: 0;
    margin-bottom: 20px;
    font-size: 12px;
    line-height: 126.19%;   

    &:last-of-type {
        margin-bottom: 44px;
    }

    @media (min-width: 992px) {
        font-size: 14px;
        line-height: 18px;

        &:last-of-type {
            margin-bottom: 55px;
        }
    }
}

.product__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.product__price {
    margin: 0;
    font-size: 18px;
    line-height: 18px;

    span {
        font-weight: 600;
    }

    @media (min-width: 992px) {
        font-size: 24px;
        line-height: 28px;
    }
}

.product__count {
    display: flex;
}

.product__count-field {
    width: 41px;
    padding: 6px;
    border: 1px solid #000000;
    text-align: center;
    height: 29px;
    -moz-appearance: textfield;
    outline: none;
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
}

.product__count-btn {
    background-color: transparent;
    text-shadow: none;
    display: block;
    padding: 0 11px;
    border: none;
    height: 29px;
    width: 30px;
    text-align: center;
    cursor: pointer;
    transition: opacity 0.5s;
    outline: none;

    &:hover,
    &:focus {
        opacity: 0.5;
    }

    @media (min-width: 768px) {
        font-size: 18px;
    }
}

.product__link {
    position: relative;
    display: block;
    color: #000000;
    text-decoration: none;
    padding-left: 19px;
    background-color: transparent;
    border: none;
    z-index: 2;
    transition: opacity 0.5s;
    outline: none;
    text-transform: uppercase;

    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 13px;
        height: 13px;
        top: 1px;
        left: 0;
        background-image: url('../img/bag.png');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &:hover,
    &:focus {
        opacity: 0.5;
    }
}

.product__slide img {
    @media (min-width: 576px) {
        width: 305px;
        height: 243px;
    }
}

.product {
    .swiper-container {
        width: 288px!important;

        @media(min-width: 576px) {
            width: 480px!important;
        }

        @media(min-width: 1152px) {
            margin-left: 70px;
            margin-right: 0;
        }
    }

    .swiper-pagination-bullet {
        width: 11px;
        height: 11px;
        background-color: #000000;
    }

    .swiper-button-next, .swiper-button-prev {
        width: 37px;
        height: 64px;
        background-image: url('../img/arrow-right.svg');
        background-repeat: no-repeat;
        background-position: center;
        outline: none;
        opacity: 0.4;

        &::after {
            display: none
        }
    }

    .swiper-button-prev {
        transform: scale(-1);
        outline: none;
    }

    .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled  {
        opacity: 0.1;
    }
}