.page-header {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 12px;
  padding-bottom: 14px;
  padding-left: 16px;
  padding-right: 16px;
  z-index: 2;

  @media (min-width: 1152px) {
    padding-top: 26px;
    padding-bottom: 20px;
    padding-left: 44px;
    padding-right: 44px;
  }
}

.page-header__logo {
  &:hover img, 
  &:focus img {
    filter: brightness(150%);
    transition: all 0.5s;
  }
}

.page-header__logo img {
  width: 62px;
  height: 22px;

  @media (min-width: 768px) {
    width: 110px;
    height: 39px;
  }
}

.page-header__list {
  display: none;
  @media (min-width: 768px) {
    max-width: 920px; 
    font-weight: 400;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 0;
    list-style: none;
  }
}

.page-header__item {
  margin-right: 38px;
  margin-bottom: 3px;
  &:last-child {
    margin-right: 0;
  }
}

.page-header__link {
  color: #000000;
  text-decoration: none;
  transition: color 0.5s;
  outline: none;

  &:hover, 
  &:focus {
    color: #959595;
  }
}

.page-header__logo-wrapper {
  display: flex;
  justify-content: space-between;
  margin-left: 40%;

  @media (min-width: 768px) {
    margin-left: 44%;
    margin-bottom: 34px;
  }
}

.page-header__user-list {
  width: 64px;
  display: flex;
  justify-content: space-between;

  @media (min-width: 1152px) {
    width: 100px;
  }
}

.page-header__user-link {
  width: 19px;
  height: 19px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: opacity 0.5s;
  outline: none;

  &--search {
    background-image: url('../img/loop.png');
  }

  &--shopping-bag {
    background-image: url('../img/bag.png');

    span {
      display: none;
    }
  }

  &--active {
    position: relative;

    span {
      position: absolute;
      bottom: -4px;
      left: 11px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background-color: #047A33;
      color: #ffffff;
      font-size: 10px;
      line-height: 12px;
    }
  }

  &:hover, 
  &:focus {
    opacity: 0.5;
  }

  @media (min-width: 1152px) {
    width: 36px;
    height: 36px;
  }
}

@media (min-width: 768px) {
  .page-header--main-slider {
    .page-header__logo-wrapper {
      margin-bottom: 16px;
    }
  
    .page-header__list {
      padding-top: 26px;
      padding-bottom: 26px;
      padding-right: 30px;
      background-color: rgba(#ffffff, 0.9);
    }

    .main-nav__toggle {
      top: 97px;
    }
  }
}

@media (min-width: 1152px) {
  .page-header--main-slider {
    .page-header__list {
      width: 795px;
      margin-left: 180px;
      margin-right: 0;
    }

    .main-nav__toggle {
      top: 107px;
    }
  }
}

