.gallery {
    position: relative;
    padding: 0 16px;
    padding-top: 24px;
    padding-bottom: 56px;
    display: grid;
    grid-template-areas: 
    'image1 image1'
    'image2 image3'
    'image2 image4';
    grid-template-columns: 58% 32%;
    grid-column-gap: 14px;
    justify-content: center;

    @media (min-width: 576px) {
        grid-template-columns: 54% 17% 26%;
        grid-template-areas: 
        'image1 image2 image3'
        'image1 image2 image4';     
    }

    @media (min-width: 1152px) {
        padding: 0 27px;
        padding-top: 71px;
        padding-bottom: 68px;
        grid-column-gap: 20px;
    }
}

.gallery__button {
    display: none;

    @media (min-width: 768px) {
        display: block;
        position: absolute;
        bottom: 56px;
        right: 35px;
        z-index: 1;
    }

    @media (min-width: 1152px) {
        bottom: 68px;
    }
}

.gallery__image {
    width: 100%;
    &--1 {
        grid-area: image1;
        margin-bottom: 17px;

        @media (min-width: 576px) {
            height: 425px;
            margin-bottom: 0;
        }
        
    }
    &--2 {
        grid-area: image2;
        height: 298px;

        @media (min-width: 576px) {
            height: 425px;
        }
    }
    &--3 {
        grid-area: image3;
        height: 128px;
        margin-bottom: 5px;

        @media (min-width: 576px) {
            height: 178px;
            margin-bottom: 17px;
        }
    }
    &--4 {
        grid-area: image4;
        height: 165px;

        @media (min-width: 576px) {
            height: 230px;
        }
    }
}