.privileges {
    background-image: url('../img/privileges.png');
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url('../img/privileges@2x.png');
    }

    @media (min-width: 768px) {
        background-image: url('../img/product-bg.png');
        background-position: right center;
        background-size: 300px 431px;
    }

    @media (min-width: 768px) {
        padding-top: 72px;
        padding-bottom: 124px;
    }   
}

.privileges__wrapper {
    @media (min-width: 1152px) {
        padding-left: 27px;
        padding-right: 27px;
    }
}

.privileges__list {
    padding: 0 16px;
    padding-top: 26px;
    padding-bottom: 72px;
    margin: 0;
    list-style: none;
    background-color: rgba(#ffffff, 0.92);
    display: grid;
    grid-row-gap: 83px;

    @media (min-width: 768px) {
        position: relative;
        padding-left: 26%;
        grid-row-gap: 90px;
        margin-left: auto;
        background-color: transparent;

        &::before {
            content: '';
            position: absolute;
            display: block;
            top: 0;
            left: 0;
            height: 100%;
            width: 24%;
            background-image: url('../img/privileges.png');
            background-repeat: no-repeat;
            background-size: cover;
        }

        @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
            &::before {
                background-image: url('../img/privileges@2x.png');
            }
        }
    }

    @media (min-width: 992px) {
        padding-left: 30%;

        &::before {
            width: 28%;
        }
    }

    @media (min-width: 1152px) {
        padding-left: 45%;
        padding-right: 0;
        padding-top: 15px;
        padding-bottom: 135px;

        &::before {
            width: 41%;
        }
    }
}

.privileges__item-text {
    position: relative;
    margin: 0;
    font-size: 12px;
    line-height: 126.19%;
    padding: 10px 0;
    padding-left: 112px;

    &::before {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        top: 50%;
        left: 0;
        width: 95px;
        height: 95px;
        transform: translateY(-50%);
        border-radius: 50%;
        border: 1px solid rgba(#000000, 0.3);
        color: rgba(#000000, 0.3);
        font-weight: 200;
        font-size: 64px;
        line-height: 75px;
    }

    &--1::before {
        content: '1';
    }

    &--2::before {
        content: '2';
    }

    &--3::before {
        content: '3';
    }

    @media (min-width: 768px) {
        font-size: 14px;
        line-height: 18px;
        padding: 0;

        &::before {
            display: none;
        }
    }
}

.privileges__item { 
    @media (min-width: 768px) {
        position: relative;
        padding-left: 143px;
    
        &::before {
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            // top: 50%;
            left: 0;
            bottom: 0;
            width: 95px;
            height: 95px;
            // transform: translateY(-50%);
            border-radius: 50%;
            border: 1px solid rgba(#000000, 0.3);
            color: rgba(#000000, 0.3);
            font-weight: 200;
            font-size: 64px;
            line-height: 75px;
        }
    
        &:nth-of-type(1)::before {
            content: '1';
        }
    
        &:nth-of-type(2)::before {
            content: '2';
        }
    
        &:nth-of-type(3)::before {
            content: '3';
        }
    }    
}

.privileges__item-title {
    @media (min-width: 768px) {
        margin-bottom: 7px;
    }
}