.news {
    padding-top: 16px;
    padding-bottom: 25px;

    @media (min-width: 768px) {
        padding: 0 16px;
        padding-top: 16px;
        padding-bottom: 25px;
    }

    @media (min-width: 1152px) {
        padding: 0 27px;
        padding-top: 35px;
        padding-bottom: 76px;
    }
}

.new__title {
    text-align: center;

    @media (min-width: 1152px) {
        margin-bottom: 45px;
    }
}

.news__item {
    margin-bottom: 23px;

    &:last-of-type {
        margin-bottom: 0;
    }

    @media (min-width: 768px) {
        display: flex;      
        min-height: 284px;
    }

    @media (min-width: 992px) {
        margin-bottom: 39px;

        &:last-of-type {
            margin-bottom: 0;
        }
    }
}

.news__item-wrapper {
    padding: 0 16px;
    padding-top: 22px;
    padding-bottom: 9px;
    background: linear-gradient(6.18deg, rgba(255, 255, 255, 0.01) 11.41%, #EFF0F4 97.32%);

    @media (min-width: 768px) {
        width: 52%;
        padding: 20px 36px;
        padding-right: 24px;
        display: flex;
        flex-direction: column;
    }

    @media (min-width: 1152px) {
        padding-right: 47px;
    }
}

.news__image {
    display: block;
    width: 100%;

    @media (min-width: 768px) {
        width: 48%;
        max-height: 284px;
    }
}

.news__item-title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
}

.news__item-text {
    margin: 0;
    margin-bottom: 15px;
    line-height: 21px;

    @media (min-width: 768px) {
        margin-bottom: 10px;
    }
}

.news__item-date {
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;

    @media (min-width: 768px) {
        margin-top: auto;
    }
}