.machine {  
    background-image: url('../img/machine-bg.png'); 
    background-repeat: no-repeat;
    background-size: cover;
    background-position: -200px center;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url('../img/machine-bg@2x.png');
    }

    @media (min-width: 768px) {
        background-size: 100% 100%;
        background-position: center;
    }

}   

.machine__wrapper {
    min-height: calc(100vh - 51px);
    padding: 16px;

    @media (min-width: 768px) {
        padding: 16px 47px;
        min-height: 541px;
    }

    @media (min-width: 992px) {
        min-height: 561px;
    }
}

.machine__title {
    margin: 0;
    font-weight: 200;
    font-size: 24px;
    line-height: 28px;
    margin-top: 35%;

    @media (min-width: 576px) {
        font-size: 40px;
        line-height: 50px;
        margin-top: 195px;
    }

    @media (min-width: 992px) {
        font-size: 72px;
        line-height: 84px;
    }
}