.vebinars {
    padding: 0 16px;
    padding-top: 23px;
    padding-bottom: 30px;

    @media (min-width: 992px) {
        padding-top: 35px;
        padding-bottom: 83px;
    }

    @media (min-width: 1152px) {
        padding-left: 27px;
        padding-right: 27px;
    }
}

.vebinars__title {
    @media (min-width: 992px) {
        margin-bottom: 60px;
    }
}

.vebinars__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-row-gap: 53px;

    @media (min-width: 576px) {
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 23px;
    }

    @media (min-width: 992px) {
        grid-row-gap: 70px;
    }
}

.vebinars__item {
    display: grid;
    grid-template-areas: 
    'demo info'
    'text text'
    'link link';
    grid-template-columns: 63px 1fr;
    grid-column-gap: 20px;

    @media (min-width: 768px) {
        grid-template-columns: 100px 1fr;
        grid-column-gap: 23px;
        grid-template-areas: 
        'demo info'
        'demo text'
        'link link';
    }

    @media (min-width: 1152px) {
        grid-template-columns: 236px 1fr;
        grid-template-areas: 
        'demo info'
        'demo text'
        'link .';
    }
}

.vebinars__video {
    grid-area: demo;
    width: 63px;
    height: 47px;
    background-image: url('../img/video.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    margin-bottom: 20px;

    @media (min-width: 768px) {
        width: 100%;
        height: auto;
        margin-bottom: 11px;
    }

    @media (min-width: 1152px) {
        height: 175px;
    }
}

.vebinars__info {
    grid-area: info;
    margin-bottom: 20px;

    @media (min-width: 768px) {
        margin-bottom: 8px;
    }
}

.vebinars__info-text {
    font-size: 14px;
    line-height: 18px;
    font-weight: 300;
    margin: 0;
    margin-bottom: 8px;

    &:last-child {
        margin-bottom: 0;
    }

    span {
        font-weight: 400;
    }
}

.vebinars__text {
    grid-area: text;
    line-height: 18px;
    margin: 0;
    margin-bottom: 23px;
    @media (min-width: 768px) {
        margin-bottom: 11px;
    }
}

.vebinars__link {
    grid-area: link;
    @media (min-width: 768px) {
        width: 100%;
    }
    @media (min-width: 1152px) {
        text-align: center;
    }
}