.main-slider {
    position: relative;
    @media (min-width: 768px) {
        margin-top: -155px;
        min-height: 675px;
    }

    @media (min-width: 1152px) {
        margin-top: -175px;
        min-height: 875px;
    }
}

.main-slider__wrapper {
    @media (min-width: 768px) {
        position: absolute;
        top: 0;
        left: 0;
    }   
}

.main-slider__slide img {
    width: 100%;
    height: 343px;
    object-fit: cover;

    @media (min-width: 768px) {
        height: 675px;
    }

    @media (min-width: 1152px) {
        height: 875px;
    }
}

.main-slider__title {
    text-align: left;
    margin-bottom: 37px;

    @media (min-width: 992px) {
        font-size: 36px;
        line-height: 42px;
        margin-bottom: 49px;
        word-break: break-all;
    }
}

.main-slider__text {
    font-weight: normal;
    margin: 0;

    &:last-of-type {
        margin-bottom: 49px;
    }
}

.main-slider__link {
    position: relative;
    display: block;
    color: #000000;
    text-decoration: none;
    padding-left: 19px;
    background-color: transparent;
    border: none;
    z-index: 2;
    transition: opacity 0.5s;
    outline: none;
    text-transform: uppercase;

    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 13px;
        height: 13px;
        top: 1px;
        left: 0;
        background-image: url('../img/bag.png');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &:hover,
    &:focus {
        opacity: 0.5;
    }
}

.main-slider__product-info {
    padding: 29px 30px 39px 30px;
    background-color: #ffffff;

    @media (min-width: 768px) {
        position: relative;
        z-index: 1;
        width: 277px;
        background-color: rgba(#ffffff, 0.9);
        padding-top: 185px;
        padding-left: 23px;
        padding-right: 16px;
        min-height: 675px;
    }

    @media (min-width: 1152px) {
        min-height: 875px;
    }
}

.main-slider {
    .swiper-pagination-bullet {
        width: 11px;
        height: 11px;
        background-color: #000000;
    }

    .swiper-scrollbar {
        display: none;
        @media (min-width: 768px) {
            display: block;
            width: calc(100% - 277px);
            margin-left: 277px;
            left: 0;
        }

        @media (min-width: 1152px) {
            display: none;
        }
    }
}