.breadcrumbs {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    font-size: 12px;
    line-height: 14px;  

    @media (min-width: 768px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.breadcrumbs__link {
    color: rgba(#000000, 0.5);
    transition: color 0.5s;

    &[href] {
        &:hover, 
        &:focus {
            color: rgba(#000000, 0.8);     
        }
    }

    &--current {
        text-transform: uppercase;
        text-decoration: none;
    }
}

.breadcrumbs__item {
    position: relative;
    padding-right: 10px;
    margin-right: 4px;

    &:last-of-type {
        margin-right: 0;
        &::before {
            display: none;
        }
    }
    &::before {
        content:">";
        position: absolute;
        right: 0;
        top: 0;
    }
}